import './styles/menunav.css'
import React, { useEffect, useState } from 'react'
import FavoriteIcon from '@material-ui/icons/Favorite'
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart'
import PersonIcon from '@material-ui/icons/Person'
import HomeIcon from '@material-ui/icons/Home'
import IconButton from '@material-ui/core/IconButton'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import { Button, Badge, Divider } from '@material-ui/core'
import HighlightOffIcon from '@material-ui/icons/HighlightOff'
//
import {useHistory} from 'react-router-dom'
//
import {useSelector, useDispatch} from 'react-redux'
import {deleteItemShopCarAction} from '../redux/bookDucks'



const MenuNav = () => {

	const [anchorEl, setAnchorEl] = React.useState(null)
	const [car, setCar] = React.useState(0)
	const carLength = useSelector(store => store.books.shopCar)
	const [menuCar, setMenuCar] = useState(false)
	const history = useHistory()
	const [bookCar, setBookCar] = useState([])
	const dispatch = useDispatch()

	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const handleClose = () => {
		setAnchorEl(null);
	};
	
	const showMenuCar = () => {
		setMenuCar(!menuCar)
		setBookCar(JSON.parse(localStorage.getItem('shopCar')))
	}

	const deleteItem = id => {
		dispatch(deleteItemShopCarAction(id))
		setBookCar(JSON.parse(localStorage.getItem('shopCar')))
	}

	const finishingShop = () => {
		history.push('/shop')
	}
	 
	useEffect(() => {
		if(localStorage.getItem('shopCar')){
			setCar(JSON.parse(localStorage.getItem('shopCar')).length)
		}else{
			localStorage.setItem('shopCar', JSON.stringify([]))
		}

	}, [carLength, car])

	return (
		<nav className="nav-container">
			<div className='nav-menu'>
				<IconButton href='/' >
					<HomeIcon fontSize="large" color='secondary'/>
				</IconButton>
				<IconButton>
					<Badge color="primary" badgeContent={0}>
						<FavoriteIcon fontSize="large" color='secondary'/>
					</Badge>
				</IconButton>
				<IconButton onClick={() => showMenuCar()}>
					<Badge color="primary" badgeContent={car}>
						<ShoppingCartIcon fontSize="large" color='secondary'/>
					</Badge>
				</IconButton>
				<div className={menuCar ? 'menu-car car-show': 'menu-car car-hide'}>
					<div className="car-body">
						{
							bookCar.map(item => (
								<div key={item.id}> 
									<div className='card-car'>
										<p className='p1'> {item.title}</p>
										<p className='p2'>${item.price}</p>
										<button onClick={ () => deleteItem(item.id)}>
											<HighlightOffIcon fontSize='large' />
										</button>
									</div>
									<Divider variant='middle'/>
								</div>
							))
						}
					</div>
					<div className="car-footer">
						<Divider variant='middle'/>
						<Button onClick={() => finishingShop()} fullWidth variant="text" color="secondary">
							Terminar de comprar
						</Button>
					</div>
				</div>
				<IconButton aria-controls="simple-menu" aria-haspopup="true" onClick={handleClick}>
					<PersonIcon fontSize="large" color='secondary'/> 
      			</IconButton>
				<Menu
					id="simple-menu"
					anchorEl={anchorEl}
					keepMounted
					open={Boolean(anchorEl)}
					onClose={handleClose}
				>
					<Button href='/login'>
						<MenuItem onClick={handleClose}>Iniciar sesión</MenuItem>
					</Button>	
					<Button href='/signup' >
						<MenuItem onClick={handleClose}>Registrarse</MenuItem>
					</Button>	
				</Menu>
			</div>
		</nav>
	)
}

export default MenuNav
