import Nav from './Nav'
import Search from './Search'
import BookCard from './BookCard'
import Footer from './Footer'
//
import React, { Fragment, useEffect } from 'react'
import Grid from '@material-ui/core/Grid'
import {useParams} from 'react-router-dom'
import { Hidden, withWidth } from '@material-ui/core'
//
import {useDispatch, useSelector} from 'react-redux'
import {getBookByTitleAction} from '../redux/bookDucks'

const SearchResults = () => {

	const books = useSelector(store => store.books.search)
	const dispatch = useDispatch()
	const {title} = useParams()

	useEffect(() => {
		const fetch = () => {
			dispatch(getBookByTitleAction(title))
		}
		fetch()
	},[dispatch, title])

	return (
		<Fragment>
			<Nav/>
			<Search 
				inSearchResults={true}
			/>
			<section className='section-container'>
				<h2>Titulo buscado: {title} </h2>
				<h4>resultados</h4>
				<Grid container justify="flex-start" direction="row" spacing={2}>
				{ 
					books.map( item => (
						<Grid item xs={12} sm={6} md={4} lg={2} xl={2} key={item.id} >
							<Hidden smDown >
								<BookCard
									price = {item.price}
									book_cover = {item.book_cover}
									rate = {item.rate}
									responsive = {false}
									id = {item.id}
									title = {item.title}
								/>
							</Hidden>
							<Hidden mdUp >
								<BookCard
									price = {item.price}
									book_cover = {item.book_cover}
									rate = {item.rate}
									responsive = {true}
									id = {item.id}
									title = {item.title}
								/>
							</Hidden>
						</Grid>
					))
				}
				</Grid>
			</section>
			<Footer />
		</Fragment>
	)
}

export default withWidth()(SearchResults)
